







































import uniSegmentedControl from '@/components/uni-segmented-control.vue';
import timeLine from '@/components/timeline.vue';
import uniFab from '@/components/uni-fab.vue';
export default {
	components: {
		uniSegmentedControl,
		uniFab,
		timeLine
	},
	data() {
		return {	
			src: "",
			timeList: [
				{title: "uni-app 支持使用 npm 安装依赖，支持微信小程序自定义组件",date: "2018.10"}
			],
			directionStr: '垂直',
			horizontal: 'right',
			vertical: 'bottom',
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#dd524d',
				buttonColor: '#ff4965'
			},
		content: [
			{
				iconPath: '/static/images/camera.png',
				selectedIconPath: '/static/images/camera.png',
				text: '拍摄',
				active: false
			},
			{
				iconPath: '/static/images/phone.png',
				selectedIconPath: '/static/images/phone.png',
				text: '图片',
				active: false
			},
			{
				iconPath: '/static/images/password1.png',
				selectedIconPath: '/static/images/password1.png',
				text: '视频',
				active: false
			}
		],
			items: [
				'图片记忆',
				'视频记录'
			],
			current: 0
		}
	},
	onUnload(){
		this.src = '',
		this.sourceTypeIndex = 2,
		this.sourceType=['拍摄', '相册', '拍摄或相册'];
	},
	methods: {	
		// 视频
		sourceTypeChange: function(e) {
			this.sourceTypeIndex = e.target.value
		},
		chooseVideo: function() {
			uni.chooseVideo({
				// sourceType: sourceType[this.sourceTypeIndex],
				success: (res) => {
					this.src = res.tempFilePath
				}
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		// 选中添加按钮中出的提示
		trigger(e) {
				console.log(e);
				this.content[e.index].active = !e.item.active;
				uni.showModal({
					title: '提示',
					content: `您${this.content[e.index].active ? '选中了' : '取消了'}${e.item.text}`,
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		}
		
	}
